	module.exports = {"data":{"post":{"html":"<p>\"Is there still a God?\"</p>\n<p>\"Why is he in heaven?\"</p>\n<p>\"Who are his customers?\"</p>\n<p>\"Are the angels his customers?\"</p>\n<p>\"What services does God perform?\"</p>\n<p>Where does Christopher come up with questions like this? Is he being a philosopher or a business analyst? (He really used the words \"Kunden\" and \"Leistungen\".) At least his final statement on religion (\"In heaven it gets dark on time.\") seemed more appropriate for his age.</p>","fields":{"title":"Is there still a God?","date":"19 Apr 2006","url":"https://papascott.de/2006-04-19-is-there-still-a-god/"}}},"pathContext":{"slug":"/2006-04-19-is-there-still-a-god/","prev":{"fields":{"title":"Steal Your Own Paper","date":"18 Apr 2006","slug":"/2006-04-18-steal-your-own-paper/"}},"next":{"fields":{"title":"Too Many Adjectives","date":"19 Apr 2006","slug":"/2006-04-19-too-many-adjectives/"}}}}

